var requirementSetModule = (function(commonModule) {

    var datatableBaseURL = commonModule.datatableBaseURL + 'requirement-set';

    var init = function() {
        _applyDatatable();
        $("#overlayForm").hide();
    };

    var addRow = function() {
        var row_table = $("table#row_table");
        var template_row = $("#new_row").find('tbody');
        var new_row = template_row.clone();
        new_row = new_row.html();
        row_table.find('tbody').append(new_row);
    };

    var removeRow = function(me) {
        me.closest('tr').remove();
    };

    var saveRequirementSet = function() {
      var callbackURL = $("input['name=callbackURL']").val();

        /* Show Overlay */
        // $("#overlayForm").show();

        /* Send Data */
        $.ajax({
            method: "POST",
            url: "/requirement-set",
            data: $('#main_form').serialize(),
            dataType: 'json'
        }).done(function(response) {
            if (response.status == 1) {
                swal({
                    title: "Good!",
                    text: response.message,
                    type: "success",
                    timer: 3000
                }, function() {
                    if(callbackURL != '')
                    {
                      window.location = "/" + callbackURL;
                    }
                    else
                    {
                      $("#main_form").trigger('reset');
                    }
                });
            } else {
                swal({
                    title: "Oops!",
                    text: response.message,
                    type: "error",
                    timer: 3000
                });
            }
            /* Hide Overlay */
            // $("#overlayForm").hide();

        });
    };

    var _applyDatatable = function() {
        /* Tambah Input Field di TFOOT */
        $('#datatable tfoot th').each(function() {
            var title = $(this).text();
            if (title !== '') {
                $(this).html('<input type="text" placeholder="Search ' + title + '" />');
            }
        });

        var table = $('#datatable').DataTable({
            processing: true,
            serverSide: true,
            ajax: {
                "url": datatableBaseURL,
                "type": "POST"
            },
            language: {
                "decimal": ",",
                "thousands": "."
            },
            columns: [{
                data: 'name',
                name: 'name'
            }, {
                data: 'created_at',
                name: 'created_at'
            }, {
                data: 'updated_at',
                name: 'updated_at'
            }, {
                data: 'action',
                name: 'action',
                orderable: false,
                searchable: false
            }]
        });

        /* Ketika Value pada Input di TFOOT berubah, Maka Search Sesuai Kolom */
        table.columns().every(function() {
            var that = this;
            $('input', this.footer()).on('keyup change', function() {
                if (that.search() !== this.value) {
                    that
                        .search(this.value)
                        .draw();
                }
            });
        });

    };

    return {
        init: init,
        addRow: addRow,
        removeRow: removeRow,
        saveRequirementSet: saveRequirementSet
    };

})(commonModule);